import React from "react";
import "./Logo.css";

const Logo = () =>
    <div className="jumbotron text-center">
        <h1 className="title"><i className="fa fa-check-square-o" aria-hidden="true"></i>Checked<span id="in">In</span></h1>
    </div>

export default Logo;


